const News = [
  {
    id: 0,
    title: '    "".',
    date: '13.01.2021',
    content: `
            ,   
       !     15%  
      .         
            .
    `,
  },
  {
    id: 1,
    title: '  !',
    date: '11.01.2021',
    content: `
       !      : 
       49 - !  27  2021     
      :  ., .14, .2. ( 2- )   
    `,
  },

  {
    id: 2,
    title:
      ' "-"     ""!',
    date: '19.04.2019',
    content: `
       !   - 3*   
      12%   - 4**    15%
            
      ""! *   - 3  -   -
        -   **   - 4
       -   -   -   -
       
    `,
  },
];

const MainNews: FC<IProps> = () => {
  return (
    <div>
      <h1 className="MainNews-heading"> </h1>
      <div className="MainNews-container">
        {News.map((news) => (
          <div
            key={news.id}
            className="MainNews-block wow slideInLeft"
            data-wow-duration="2s">
            <a className="MainNews-link" href="/">
              {news.title}
            </a>
            <p className="MainNews-data">{news.date}</p>
            <p className="MainNews-item">{news.content}</p>
          </div>
        ))}
      </div>
    </div>
  );
};

export default MainNews;
