import Modal from '../Modal/Modal';
import './FeedbackForm.scss';
import React, { FC } from 'react';

interface IExternalProps {
  onClose?: () => void;
  visible: boolean;
}

interface IProps extends IExternalProps {}

const FeedbackForm: FC<IProps> = ({ visible, onClose }) => {
  return (
    <Modal
      cancelText=""
      okText=""
      visible={visible}
      onClose={onClose}>
      <div>
        <h2 className="FeedbackForm-title"> </h2>
        <p className="FeedbackForm-paragraph"> :</p>
        <input className="textField"></input>
        <p className="FeedbackForm-paragraph">T:</p>
        <input className="textField"></input>
        <p className="FeedbackForm-paragraph">email:</p>
        <input className="textField"></input>
        <p className="FeedbackForm-paragraph">:</p>
        <textarea className="textarea"></textarea>
      </div>
    </Modal>
  );
};

export default FeedbackForm;
