import { FC } from 'react';
import Modal from '../Modal/Modal';
import './FloatingButtonModal.scss';

interface IExternalProps {
  onClose?: () => void;
  visible: boolean;
}

interface IProps extends IExternalProps {}

const FloatingButtonModal: FC<IProps> = ({ visible, onClose }) => {
  return (
    <Modal
      cancelText=""
      okText=""
      visible={visible}
      onClose={onClose}>
      <div>
        <h1 className="FloatingButtonModal-title">  </h1>
        <p className="FloatingButtonModal-paragraph"> :</p>
        <input className="textField"></input>
        <p className="FloatingButtonModal-paragraph">T:</p>
        <input className="textField"></input>
        <p className="FloatingButtonModal-paragraph">  :</p>
        <input className="textField"></input>
        <p className="FloatingButtonModal-paragraph">
           :
        </p>
        <textarea className="textarea"></textarea>
      </div>
    </Modal>
  );
};

export default FloatingButtonModal;
